.section {
	background-color: getColor("revers-bg-color");
	padding-bottom: 40px;
	margin: 0 auto;
	padding-top: 125px;
	background-image: url(../images/about/about-milk-mob.png);
	background-size: contain;
	background-repeat: no-repeat;
	background-position: left bottom 150px;
	@media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
		background-image: url(../images/about/about-milk-mob-x2.png);
	}
	@media screen and (min-width: 768px) {
		background-image: none;
	}
	@media screen and (min-width: 1280px) {
		//background-size: 1280px;
		background-position: bottom;
		background-image: url(../images/about/about-milk-desc.png);
		@media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
			background-image: url(../images/about/about-milk-desc-x2.png);
		}
	}
}
.about-title {
	font-family: Titan One;
	font-style: normal;
	font-weight: 500;
	font-size: 18px;
	line-height: 1.16;
	text-align: center;
	text-transform: uppercase;
	color: getColor("pink-color");
	margin-bottom: 15px;

	@media screen and (min-width: 768px) {
		font-size: 26px;
		line-height: 1.15;
	}
	@media screen and (min-width: 1280px) {
		font-size: 30px;
		line-height: 1.13;
	}
}
.about-text {
	font-family: Titan One;
	font-style: normal;
	font-weight: 500;
	font-size: 34px;
	line-height: 1.14;
	text-align: center;
	letter-spacing: 0.04em;
	text-transform: uppercase;
	color: getColor("link-hf-color");
	position: relative;
	@media screen and (min-width: 768px) {
		font-size: 48px;
		line-height: 1.14;
	}
	@media screen and (min-width: 1280px) {
		font-size: 58px;
		line-height: 1.13;
	}
}
.about-wrap-text {
	padding: 45px 25px;

	width: 300px;
	justify-content: center;
	//margin: 0 auto;
	@media screen and (min-width: 480px) {
		min-width: 370px;
	}
	@media screen and (min-width: 768px) {
		min-width: 450px;
	}
	@media screen and (min-width: 1200px) {
		padding-left: 74px;
	}
}
.about-text-one {
	font-weight: 500;
	font-size: 12px;
	line-height: 1.62;
	color: getColor("black-color");
	margin-bottom: 47px;
	@media screen and (min-width: 768px) {
		font-size: 14px;
		line-height: 1.74;
	}
	@media screen and (min-width: 1280px) {
		font-size: 16px;
		line-height: 1.76;
	}
}
.about-text-two {
	font-weight: 500;
	font-size: 12px;
	line-height: 1.62;
	color: getColor("another-color");
	margin-bottom: 20px;
	@media screen and (min-width: 768px) {
		font-size: 14px;
		line-height: 1.76;
	}
	@media screen and (min-width: 1280px) {
		font-size: 14px;
		line-height: 1.94;
	}
}
.about-btn {
	font-weight: 700;
	font-size: 14px;
	text-align: center;
	@media screen and (min-width: 320px) {
		margin: 0 auto;
	}
	@media screen and (min-width: 768px) {
		margin: 0;
	}
	&:hover,
	&:focus {
		transition-duration: 250ms;
		transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
		transition-property: transform;
	}
}
.about-svg {
	font-weight: 700;
	font-size: 14px;
	line-height: 1.5;
	margin-left: 14px;
}
.hi {
	@media screen and (min-width: 768px) {
		display: flex;
	}
}
