* {
  border: 0;
  margin: 0;
  padding: 0;
}

:focus, :active, a:focus, a:active {
  outline: none;
}

nav, footer, header, aside {
  display: block;
}

html, body {
  height: 100%;
  width: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  font-size: 100%;
}

input, button, textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

a, a:visited {
  font-style: normal;
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: inherit;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

body {
  letter-spacing: .04em;
  background: #fff;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  line-height: 1.52;
}

.no-scrol {
  overflow: hidden;
}

.container {
  max-width: 480px;
  box-sizing: border-box;
  margin: 0 auto;
  padding: 0 20px;
}

.selector {
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px;
  width: 1px;
  border: 0;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.button--red, .button--white {
  cursor: pointer;
  border-radius: 20px;
  align-items: center;
  padding: 10px 23px;
  line-height: 1.28;
  transition: background-color .3s cubic-bezier(.075, .82, .165, 1);
  display: flex;
}

.button--red {
  color: #fff;
  background-color: #d41342;
}

.button--red:hover, .button--red:focus {
  background-color: #c7133e;
}

.button--white {
  color: #d41443;
  background-color: #fff;
}

.button--white:hover, .button--white:focus {
  background-color: #ecebeb;
}

